body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #889;
  padding: 2rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.salary {
  color: purple;
}
.App {
}
.center {
  text-align: center;
}
.App h1 {
  letter-spacing: 2rem;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-size: 3rem;
}

.input {
  padding: 0.5rem 1rem;
  border: 1px solid #889;
  border-radius: 0.5rem;
  color: #889;
  font-size: 2rem;
}
.input:focus {
  border: 1px solid #aaa;
  outline: none;
}

.input.rupee {
  background-image: url(http://i.imgur.com/Fd7NROx.png);
  border: 1px solid #aaa;
  padding-left: 3%;
  background-size: 20px 25px; /* Change symbol size */
  background-repeat: no-repeat; /* So that rupee image doesn't repeat */
  background-position-y: 55%;
  background-position-x: 3%;
}

* {
  font-family: inherit;
  box-sizing: border-box;
}

.cc {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  display: inline-block;
  margin-right: 1%;
}
.income-container {
  width: 100%;
  height: 4rem;
  background-color: plum;
  margin-bottom: 1rem;
  position: relative;
  color: white;
  border-radius: 1rem;
  overflow: hidden;
}
.income-container .total-tax {
  position: absolute;
  left: 0;
  top: 0;
  width: 5%;
  background-color: #777;
  z-index: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.income-container .total-tax div {
  position: absolute;
  left: 0;
  top: 0;
  width: 5%;
  background-color: #666;
  z-index: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.income-container .total-tax div:nth-child(2) {
  background-color: #555;
}

@media only screen and (max-width: 700px) {
  .App h1 {
    font-size: 30px;
    letter-spacing: 10px;
  }
  input {
    font-size: 1rem;
    width: 100%;
  }
  .input.rupee {
    padding-left: 10%;
  }
}
